import logo from './logo.svg';
import './App.css';

import { createStaticPix, parsePix } from 'pix-utils';
import { useEffect, useState, useRef } from 'react';
import { useForm } from "react-hook-form";
import ReactToPrint from 'react-to-print';
import { useReactToPrint } from 'react-to-print';
import QrCode from './qrcode';

const pageStyle = `
  @page {
    size: 80mm 80mm;
  }

  @media all {
    .pagebreak {
      display: none;
    }
  }

  @media print {
    .pagebreak {
      page-break-before: always;
    }
  }
`;


function App(){
  const [qrcode, setQrCode] = useState();
  const [loading, setLoading] = useState(false);
  const [generateQrCode, setGenerateQrCode] = useState(false);
  const [firstTime, setFirstTime] = useState(false);
  const [value, setValue] = useState(0.0);
  const { register, handleSubmit, formState: { errors } } = useForm();
  const componentRef = useRef();
  
  const onSubmit = (data) =>{
    setFirstTime(true);
    //setGenerateQrCode(!generateQrCode);
    setValue(data.amount);
  }

  useEffect(() => {
    console.log("entrei no change value");
    setLoading(true);
    const pix = createStaticPix({
      merchantName: 'Burger House SMI',
      merchantCity: 'SMI',
      pixKey: '32961536000160',
      infoAdicional: 'Pagamento para Burger House',
      transactionAmount: parseFloat(value),
      txid: '',
    });
    
    async function getQrCode(){
      try{
        setQrCode(await pix.toImage());
      }catch (e){
        console.log(e);
      } finally {
        setLoading(false);
      }
    }
    getQrCode();
  }, [value]);

  return (
    <div className="App">
      <div>
        Insira o valor do PIX: 
      </div>
      <div>
      <form onSubmit={handleSubmit(onSubmit)}>
        <label>
          Valor:
        </label>
        <div style={{
          flexDirection: "column",
          maxWidth: "150px",
        }}>
          <input style={{margin: 10}} type="number" step="0.01" {...register("amount", { required: true, min: 1 })} />
          {errors.amount && <span>Esse campo é obrigatório</span>}
          <input style={{margin: 10}} type="submit" value="Gerar QR Code!"/>
        </div>
      </form>
      </div>
      {firstTime ? loading ? "Carregando" : 
      <div>
        <div ref={componentRef}>
          <QrCode qrcode={qrcode}/>
        </div>
        <ReactToPrint
          trigger={() => <button>Imprimir QrCode!</button>}
          content={() => componentRef.current}
          pageStyle={pageStyle}
        />
      </div> : 
      "Favor inserir um valor"}
    </div>
  );
}

export default App;