import { Buffer } from 'buffer';
import axios from 'axios';
import zeroPad from './utils/zeroPad';
export default async function fetchPayload({ url, DPP = new Date().toISOString().substr(0, 10), codMun = 5300108, }) {
    const axiosOptions = {
        params: {
            DPP,
            codMun: zeroPad(codMun, 7),
        },
    };
    return axios
        .get('https://' + url, axiosOptions)
        .then(({ data, status }) => {
        if (status !== 200)
            return { error: true, message: 'Status != 200' };
        return data;
    })
        .then((jws) => {
        const parts = jws.split('.').map((b64) => Buffer.from(b64, 'base64'));
        const pixFetch = {
            jwsString: jws,
            jws: {
                hdr: parts[0],
                payload: parts[1],
                signature: parts[2],
            },
            header: JSON.parse(parts[0].toString()),
            payload: JSON.parse(parts[1].toString()),
        };
        return pixFetch;
    })
        .catch((error) => {
        console.error(error);
        return { error: true, message: error.message };
    });
}
